.care {
  margin-top: 174px;
  margin-bottom: 106px;
  color: var(--color-light);

  &__container {
    padding: 0 25px;
  }

  &__inner {
    margin: 0 auto;
    max-width: 805px;
    padding-left: 5.5%;
    @media (max-width: 650px) {
      padding-left: 0;
    }
  }

  &__title {
    margin-bottom: 39px;
    font-size: 36rem;
    font-weight: 600;
    @media (max-width: 750px) {
      font-size: 32rem;
    }
  }

  &__contacts {
    margin-top: 34px;
    margin-bottom: 30px;

  }

  & p {
    font-size: 18rem;
    font-weight: 800;
    line-height: 27px;
    &:not(:last-child) {
      margin-bottom: 27px;
    }
    @media (max-width: 750px) {
      font-size: 16rem;
    }
  }

  &__address {
    font-size: 18rem;
    font-weight: 300;
    line-height: 27px;
    font-style: normal;
    @media (max-width: 750px) {
      font-size: 16rem;
    }

    & a {
      display: block;
      &:not(:last-child) {
        margin-bottom: 25px;
      }
      & span {
        text-decoration: underline;
      }
    }

  }
}
